@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:ital,wght@1,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.stars_cont_rating{
    display: flex;
    flex-flow: row wrap;
    margin: 0px;
}



.stars_cont {
    display: flex;
    flex-flow: row nowrap;
}

.stars {
    display: flex;
    flex-flow:row nowrap;
    margin: auto 3px;
}

.stars > div{


    display: flex;
    flex-flow:row nowrap;
    margin: auto 3px;
}

.Comentario_item{
    margin: 20px 5px;
    display: flex;
    flex-flow: column wrap;

}

.Comentario_item  .cabezera_coment {

    padding: 5px;
    margin: auto 5px;

    background-color: rgb(168, 233, 219);


    display: flex;
    flex-flow: row wrap;

    justify-content:space-around;
    align-items: center;


}

.Comentario_item  .cabezera_coment  div  {
    display: flex;
    flex-flow:  row wrap;
    align-items: center;

}
.Comentario_item  .cabezera_coment  div > *  {
    margin: 5px;
}


.comentario_texto{
    min-height: 25px;
    background-color: azure;
    border-radius: 0px 0px 5px  5px;

    padding: 5px;
    margin: auto 5px;

}


.img_user{
    width: 40px;
    height: 40px;
}

.Cont_prin_detalles{
    height: 600px;
    overflow-y: scroll;
}
.Cont_detalles_imagenes{
    display: flex;
    flex-flow: row wrap;
    margin: auto;

    justify-content: center;
    align-items: center;

    padding: 5px;
    overflow: hidden;

}
.img_detalles{
    overflow: hidden;

    margin: 3px;

    border: 1px solid black;

    width: 230px;
    height: 200px;

}

.Cont_detalles_horarios{
    text-align: left;
    display: flex;
    flex-flow: row wrap;
}

.Cont_prin_Lugares_cercanos{
    height: 600px;
    overflow-y: scroll;

}
.lugares_item{
  margin-bottom: 30px;
}
.lugares_item > div{
  margin:auto;
  width: 250px
}
.lugares_item > div button{
    padding: 5px 25px;
    background-color: #9d71ff;
    border: none;
    box-shadow: none
}
.lugares_item > div button:hover{
  background-color: #a982ff;
}


.lugares_item img{
  margin: 3px;

  width: 250px;
  height: 200px;
}

.container_resultados{
    width: 100%;
    margin:30px auto;

    display: flex;
    flex-flow: row nowrap;
    padding: 20px ;
  }

.resultado_item{
    width: 50%;
    max-width: 580px;

    min-width: 400px;
    min-height: 600px;

    margin:auto;
    padding: 20px 5px;


    background: #d7d5cd;


    overflow: hidden;
}

.resultado_item + p{
    text-align: center;
}

.resultado_item >img{
    max-width: 300px;
    max-height: 250px;

}

.resultado_item button[class^='button']{
    margin:2px ;

    display: inline-block;
    background-color: rgb(93, 93, 138);
    border: none;
    color: white;
    padding: 5px 20px;
}


.tipo_local{
    padding: 5% 10% ;
    display: flex;
    flex-flow: column wrap;
    text-align: start;
}
.tipo_local p{
  margin:0px;
}


.resultado_detalles{

    width: 96%;
    padding: 20px 5px;
    display: none;
    background: #54c435;
    animation-duration: 1s;
}
.lugares_cercanos{
  width: 96%;
  padding: 20px 5px;
  display: none;
  background: #54c435;
  animation-duration: 1s;
}


@keyframes aparece{
    from{
        width: 0%;
    }
    to{
        width: 96%;
    }
}

@keyframes desaparece{
    from{
        width: 96%;
    }
    to{
        width: 0%;
    }
}

@media (max-width:900px){
    .container_resultados{
        width: 90%;
        margin: 30px auto;


        flex-flow: column wrap;
      }

    .resultado_item{
        width: 96%;
        min-width: auto;
        min-height: 300px;

    }
    .resultado_detalles{
        margin: auto ;
        height: auto;
        max-width: 580px;
        width: 96%;
    }
    .lugares_cercanos{
      margin: auto ;
      height: auto;
      max-width: 580px;
      width: 96%;
    }
    .tipo_local{
      padding: 5%;
    }


}


h1,h2,h3,h4,h5,h6{
  font-family: 'Fira Sans', sans-serif;
}
p,strong{
  font-family: 'Quicksand', sans-serif;
}
ul {
  list-style-type: square;
}


.container_app{
  width: 90%;
  margin: 20px auto ;

  text-align: center;

  display: flex;
  flex-flow: column wrap;
}

.container_map{
  width: 90%;
  box-shadow: 1px 1px 1px 1px rgb(92, 80, 80);
  margin: auto;

  display: flex;
  flex-flow: row wrap;

}

#map{
  margin: auto;
  width: 60%;
  height: 700px;
  box-shadow: 1px 1px 1px 1px black;

}

#buscador{
  width: 40%;
  display: flex;
  flex-flow: column nowrap;

  align-items: center;
  padding: 30px 0px;
}
#buscador input{
  margin: 10px;

  width: 70%;
  height: 28px;

  border: none;
  box-shadow: none;
  outline: none;

  border-bottom: solid #333646  1px  ;
  background-image: url(/React_proyect_maps/static/media/search-solid.cc51b702.svg);
  background-size: 20px ;
  background-position: right;
  background-repeat: no-repeat;
}
#buscador input:focus{
  background-color: none;

}

#buscador button{
  padding: 7px 35px;

  border: none;

  background-color: rgb(156, 212, 99);
  color: rgb(255, 255, 255);
}
#buscador button:hover{
  background-color: rgb(167, 204, 131);
}
#opciones{
  padding: 5px 30px;
  border-radius: 0px;
  border: 0.5px solid rgb(156, 212, 99);
}
#opciones:hover{
  background-color: rgb(167, 204, 131);
}

@media (max-width:900px){
  .container_app{
    margin-top: 0px;
    width: 100%;
  }
  .container_map{
    width: 100%;
    box-shadow: none;
    flex-flow: column wrap;
  }
  .container_resultados{
    padding: 0px;
  }


  #map{
    width: 100%;
    height: 300px;
  }
  #buscador{
    margin-bottom: 100px;
    width: 100%;
  }
  #buscador input{
    width: 85%;
    max-width: 300px;
  }



}

